.test-container {
  width: 100%;
  max-width: 800px;
  background: white;
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0 6px 18px rgba(0,0,0,.08);
  margin: 20px auto;
}

.test-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.timer {
  font-size: 18px;
  font-weight: bold;
  color: #2c3e50;
}

.question {
  margin-bottom: 25px;
  text-align: left;
}

.options {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  gap: 10px;
  margin-top: 10px;
}

.option-btn {
  padding: 14px;
  border: 2px solid #3498db;
  border-radius: 8px;
  text-align: center;
  background: #fff;
  color: #3498db;
  font-weight: bold;
  cursor: pointer;
  transition: 0.2s;
  user-select: none;
}

.option-btn:hover {
  background: #3498db;
  color: #fff;
}

.option-btn.selected {
  background: #27ae60;
  border-color: #27ae60;
  color: white;
}

.text-input {
  width: 100%;
  padding: 12px;
  font-size: 18px;
  border: 2px solid #ddd;
  border-radius: 8px;
  box-sizing: border-box;
  margin-top: 10px;
}

.text-input:focus {
  border-color: #3498db;
  outline: none;
}

.submit-btn {
  width: 100%;
  padding: 16px;
  background: #3498db;
  color: white;
  font-size: 18px;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  margin-top: 20px;
  transition: background 0.3s;
}

.submit-btn:hover {
  background: #2980b9;
}

.user-id {
  margin-bottom: 10px;
  font-weight: bold;
  color: #555;
}

/* Стили для математического ввода */
.math-input-container {
  margin-top: 10px;
  border: 2px solid #ddd;
  border-radius: 8px;
  padding: 10px;
}

.math-input-container:focus-within {
  border-color: #3498db;
}

/* Адаптация существующих стилей */
.text-input {
  width: 100%;
  padding: 12px;
  font-size: 18px;
  border: 2px solid #ddd;
  border-radius: 8px;
  box-sizing: border-box;
  margin-top: 10px;
}

.text-input:focus {
  border-color: #3498db;
  outline: none;
}

.error-message {
  color: #dc3545;
  margin-bottom: 15px;
  padding: 8px;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 4px;
} 
