.page-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f5f8ff;
}

.body {
  background: white;
  padding: 30px 40px;
  border-radius: 12px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 400px;
}

.results-info {
  text-align: left;
  margin-bottom: 20px;
}

.results-info p {
  margin: 10px 0;
  font-size: 16px;
}

.success-message {
  background-color: #d4edda;
  color: #155724;
  padding: 15px;
  border-radius: 8px;
  border: 1px solid #c3e6cb;
  margin-top: 20px;
}

