.page-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f5f8ff;
}

.body {
  background: white;
  padding: 30px 40px;
  border-radius: 12px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 320px;
}

.title h1 {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 15px;
}

.form-label {
  display: block;
  font-size: 14px;
  color: #444;
  margin-bottom: 10px;
  text-align: left;
}

.registration-text-input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
  margin-bottom: 15px;
  outline: none;
  box-sizing: border-box;
}

.submit-btn {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  border: none;
  border-radius: 6px;
  color: white;
  font-size: 15px;
  font-weight: 500;
  cursor: pointer;
  transition: 0.3s;
}

.submit-btn:hover {
  background-color: #0069d9;
}
